.login {
  width: 100%;
  max-width: 1000px;
  display: flex;
  align-self: flex-end;
  justify-self: center;
  flex-direction: column;
  margin: auto;
  padding: 5px;
}
.litopbar {
  width: 100%;
}
.lihead {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0;
  font-size: 22px;
  font-weight: 650;
}
.li_sec {
  width: 100%;
  border: 1px solid grey;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.li_sec-head {
  background-color: rgb(222, 222, 222);
  width: 100%;
  border: 1px solid rgb(183, 179, 164);
  border-radius: 10px 10px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  font-weight: 650;
  border-bottom: 1px solid gray;
}
.li_sec-bottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 10px;
  padding: 5px;
}
.li_sec-bottom .li_sec-left {
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.li_sec-bottom span {
  width: fit-content;
  text-wrap: none;
  white-space: nowrap;
}
.li_sec-bottom .li_sec-left input {
  width: 100%;
  max-width: 200px;
  border-radius: 20px;
  border: 1px solid gray;
  margin-left: 10px;
  padding: 5px;
  outline: none;
}

.li_sec-bottom .li_sec-right {
  width: fit-content;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.li_sec-bottom .li_sec-right input {
  width: 100%;
  max-width: 250px;
  border: 1px solid gray;
  margin-left: 10px;
  padding: 5px;
  border-radius: 0;
  outline: none;
}
.li_down {
  width: 100%;
  border: 1px solid grey;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30px;
}

.li_down-top {
  background-color: rgb(222, 222, 222);
  width: 100%;
  border: 1px solid rgb(183, 179, 164);
  border-radius: 10px 10px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  font-weight: 650;
  border-bottom: 1px solid gray;
}

.li_down-bottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  padding: 5px;
}
.lidb_down {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.lidb_down input {
  width: 100%;
  max-width: 200px;
  border-radius: 20px;
  border: 1px solid gray;
  margin-left: 10px;
  padding: 5px;
}
.login button {
  padding: 5px;
  background-color: white;
  margin-left: 10px;
  border-radius: 20px;
  border: 1px solid gray;
}
.loginfoot {
  width: 100%;
}
