.topbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid gray;
  background-color: #e8ece0;
  border-radius: 20px;
  text-align: center;
}
.tb_logo a {
  font-size: 24px;
  font-weight: 800;
  color: rgb(1, 81, 0);
  padding: 5px 0 0 0;
}
.tb_mid {
  padding: 5px 0 0 0 0;
}
.tb_down {
  padding: 5px 0 0 0;
}

.tb_bottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0;
}

.tb_bottom input {
  width: 70%;
  background-color: #fff;
  border-radius: 20px;
  padding: 5px;
  border: 1px solid rgb(183, 179, 164);
  max-width: 500px;
  margin-right: 5px;
  outline: none;
}
.tb_bottom button {
  background-color: #fff;
  border-radius: 5px;

  background-color: #fff;
  border-radius: 20px;
  padding: 5px;
  border: 1px solid rgb(198, 198, 198);
}
a {
  text-decoration: none;
  padding-left: 3px;
}

.ht {
  width: 100%;
  display: flex;
  margin-top: 10px;
  flex-direction: column;
}
.ht_sec {
  width: 100%;
  border: 1px solid rgb(183, 179, 164);
  border-top: none;
  padding: 5px;
  display: flex;
  align-self: flex-start;
  justify-self: flex-start;
}

.ht_sec a {
}

.ht_sec:first-child {
  background-color: rgb(222, 222, 222);
  width: 100%;
  border: 1px solid rgb(183, 179, 164);
  border-radius: 10px 10px 0 0;
  padding: 5px;
  font-weight: 700;
  color: #200;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ht_sec b {
  width: 100%;
  display: flex;
  align-items: center;
  justify-self: flex-start;
  flex-wrap: wrap;
}

.ht_sec:last-child {
  border-radius: 0 0 10px 10px;
}
.tb_ads {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 10px;
}
.tba_secs {
  font-size: 25px;
  letter-spacing: 2px;
  color: rgb(12, 106, 0);
  width: 100%;
  text-align: center;
  border-radius: 10px;
  border: 1px solid rgb(183, 179, 164);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  max-width: 310px;
}
@media only screen and (max-width: 600px) {
  .tba_secs.a {
    display: none;
  }
  .tba_secs.a {
    display: none;
  }
}
