.open {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.op_head {
  font-size: 22px;
  font-weight: 650;
}

.open_head {
}
.opennews {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
}
.opennews_top {
  width: 100%;
  border: 1px solid rgb(151, 151, 151);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
  padding: 5px;
  background: #3a6f3a26;
  font-weight: 700;
  border-radius: 10px 10px 0 0;
  margin-top: 10px;
  padding-left: 10px;
}
.opennews_mid {
  width: 100%;
  min-height: 50px;
  border-top: 1px solid rgb(182, 182, 182);
  border: 1px solid rgb(182, 182, 182);
  border-radius: 0 0 10px 10px;
  padding: 10px;
}
.opennews_head {
  margin-top: 10px;
  text-align: center;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  font-weight: 650;
  color: #200;
  margin-bottom: 10px;
}
.opennews_bc {
  font-weight: 700;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  color: blue;
  text-align: center;
}
.opennews_mid blockquote {
  width: 100%;
  border-radius: 10px;
  background-color: #99c49e;
  border: 1px solid gray;
  padding: 5px;
  overflow: hidden;
}
.opennews_mid img {
  width: 80%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.opennews_mid .narrow {
  overflow: hidden;
}
