.footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.ft_up {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgb(222, 222, 222);
  border: 1px solid rgb(183, 179, 164);
  border-radius: 10px;
  padding: 5px;
  margin-top: 10px;
}
.ft_top {
  font-weight: 700;
}
.ft-bottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 10px;
}
.ftb-bottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 12px;
}
.ft_down a {
  font-weight: 650;
}
.footerbottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgb(222, 222, 222);
  border: 1px solid rgb(183, 179, 164);
  border-radius: 10px;
  padding: 5px;
  font-size: 14px;
  margin-top: 10px;
}

.ftb_top {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0;
}

.ftb_top input {
  width: 70%;
  background-color: #fff;
  border-radius: 20px;
  padding: 5px;
  border: 1px solid rgb(183, 179, 164);
  max-width: 500px;
  outline: none;
}
.ftb_top button {
  background-color: #fff;
  border-radius: 5px;
  margin-left: 5px;
  background-color: #fff;
  border-radius: 20px;
  padding: 5px;
  border: 1px solid rgb(182, 182, 182);
}
.myfooter {
  background: #000000b3;
  color: #ffcaed;
  width: 100%;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0;
  font-size: 12px;
  font-weight: 600;
}
.myfoot {
  width: 100%;
  max-width: 700px;
  padding: 0 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mfleft {
}
.mfright {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 5px;
}
.mficon {
  width: 30px;
  height: 30px;
  color: #ffcaed;
}
.mficon:hover {
  transform: scale(1.1);
  color: #ff90da;
}
.mfpurple {
  color: #7839ff;
  text-decoration: none;
}
.mfpurple:hover {
  color: #9c6eff;
  text-decoration: none;
}
.loginfoot {
  margin-top: 20px;
}
