.forums {
  width: 100%;
  max-width: 1000px;
  display: flex;
  align-self: flex-end;
  justify-self: center;
  flex-direction: column;
  margin: auto;
  padding: 5px;
}
.foh_topbar {
  width: 100%;
}
.forum_head {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.foh_top {
  font-weight: 650;
  font-size: 20px;
}

.foh_mid {
  font-weight: 600;
}
.foh-bottom {
  width: 100%;
}
.foh_ads {
  width: 100%;
}
.foh_news {
  width: 100%;
}
.foh_news-top {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  padding: 5px;
  border: 1px solid rgb(198, 198, 198);
  border-radius: 10px 10px 0px 0px;
  background-color: rgb(222, 222, 210);
}
.foh_news-mid {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid rgb(208, 208, 208);
  min-height: 200px;
  border-radius: 0 0 10px 10px;
}
.foh_pages {
  padding: 10px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.foh_fbottom {
  width: 100%;
}

.foh_news-mid a {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-bottom: 1px solid rgb(200, 200, 200);
  font-weight: 650;
}
.foh_news-mid a:last-child {
  border-bottom: none;
}
