.otherpages {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 5px;
}
.ophead {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.ophead span:first-of-type {
  padding: 10px;
  font-size: 22px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #200;
}
.op_pagesbottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  font-weight: 600;
  margin-bottom: 5px;
  margin-top: 5px;
}
.op_pagesbottom > * {
  padding: 1px;
}
.up {
  font-weight: 650;
  font-size: 14px;
}
.op_newspage {
  width: 100%;
}
.op_mid {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid gray;
  border-radius: 10px;
}
.op_mid-news {
  width: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.op_mid-news div {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid gray;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
}
.op_mid b {
  padding-left: 5px;
}
