.newssec {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 10px;
}
.ns_top {
  background-color: rgb(222, 222, 222);
  width: 100%;
  border: 1px solid rgb(183, 179, 164);
  border-radius: 10px 10px 0 0;
  padding: 5px;
  font-weight: 700;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ns_mid {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid rgb(183, 179, 164);
  border-top: 0;
  border-bottom: 0;
}

.ns_mid a {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid rgb(183, 179, 164);
  border-top: 0;
  border-bottom: 0;
  border-bottom: 1px solid rgb(189, 188, 180);
}

.ns_bottom {
  background-color: rgb(222, 222, 222);
  width: 100%;
  border: 1px solid rgb(183, 179, 164);
  border-radius: 0 0 10px 10px;
  padding: 5px;
  font-weight: 700;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ns_mid-news {
  width: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: row;
  padding: 5px;
}

.ns_mid-news b {
  margin-left: 5px;
  margin-right: 5px;
}
